	module.exports = {"data":{"postsJson":{"title":"Paul's Conversion","questionsTitle":"QUESTIONS, QUESTIONS, QUESTIONS","display":"block","bookName":"Atos","ref":"8:5-9:31","html":"<p>   <strong>8></strong><u>4</u>Aqueles que tinham sido espalhados anunciavam o evangelho por toda parte. <u>5</u>Filipe foi até a capital da Samaria e anunciava Cristo às pessoas dali, <u>6</u>e as multidões ouviam com atenção o que ele dizia. Todos o escutavam e viam os milagres que ele fazia. <u>7</u>Os espíritos maus, gritando, saíam de muitas pessoas, e muitos coxos e paralíticos eram curados. <u>8</u>E assim o povo daquela cidade ficou muito alegre.<br />   <u>9</u>Morava ali um homem chamado Simão, que desde algum tempo atrás fazia feitiçaria entre os samaritanos e os havia deixado muito admirados. Ele se fazia de importante, <u>10</u>e os moradores de Samaria, desde os mais importantes até os mais humildes, escutavam com muita atenção o que ele dizia. Eles afirmavam:</p><h1>Moradores de Samaria</h1><h4>Este homem é o poder de Deus! Ele é 'o Grande Poder'!\"</h4><p><u>11</u>Eles davam atenção ao que Simão fazia porque durante muito tempo ele os havia deixado assombrados com as suas feitiçarias. <u>12</u>Mas eles acreditaram na mensagem de Filipe a respeito da boa notícia do Reino de Deus e a respeito de Jesus Cristo e foram batizados, tanto homens como mulheres. <u>13</u>O próprio Simão também creu. E, depois de ser batizado, acompanhava Filipe de perto, muito admirado com os grandes milagres e maravilhas que ele fazia.<br />   <u>14</u>Os apóstolos, que estavam em Jerusalém, ficaram sabendo que o povo de Samaria também havia recebido a palavra de Deus e por isso mandaram Pedro e João para lá. <u>15</u>Quando os dois chegaram, oraram para que a gente de Samaria recebesse o Espírito Santo, <u>16</u>pois o Espírito ainda não tinha descido sobre nenhum deles. Eles apenas haviam sido batizados em nome do Senhor Jesus. <u>17</u>Aí Pedro e João puseram as mãos sobre eles, e assim eles receberam o Espírito Santo.<br />   <u>18</u>Simão viu que, quando os apóstolos punham as mãos sobre as pessoas, Deus dava a elas o Espírito Santo. Por isso ofereceu dinheiro a Pedro e a João, <u>19</u>dizendo:</p><h1>Simão, o feiticeiro</h1><h4>Quero que vocês me dêem também esse poder. Assim, quando eu puser as mãos sobre alguém, essa pessoa receberá o Espírito Santo.</h4><p><u>20</u>Então Pedro respondeu:</p><h1>Simão Pedro</h1><h3>Que Deus mande você e o seu dinheiro para o inferno! Você pensa que pode conseguir com dinheiro o dom de Deus? <u>21</u>Você não tem direito de tomar parte no nosso trabalho porque o seu coração não é honesto diante de Deus. <u>22</u>Arrependa-se, deixe o seu plano perverso e peça ao Senhor que o perdoe por essa má intenção. <u>23</u>Vejo que você está cheio de inveja, uma inveja amarga como fel, e vejo também que você está preso pelo pecado.</h3><p><u>24</u>Aí Simão disse a Pedro e a João:</p><h1>Simão, o feiticeiro</h1><h4>Por favor, peçam ao Senhor por mim para que não aconteça comigo nada do que vocês disseram.</h4><p><u>25</u>Depois de terem dado o seu testemunho e de terem pregado a palavra do Senhor, Pedro e João voltaram para Jerusalém. No caminho eles espalhavam o evangelho em muitos povoados da Samaria.<br />   <u>26</u>Um anjo do Senhor disse a Filipe:</p><h1>Um Anjo</h1><h4>Apronte-se e vá para o Sul, pelo caminho que vai de Jerusalém até a cidade de Gaza.</h4><p>(Pouca gente passava por aquele caminho.)<br />   <u>27-28</u>Filipe se aprontou e foi. No caminho ele viu um eunuco da Etiópia, que estava voltando para o seu país. Esse homem era alto funcionário, tesoureiro e administrador das finanças da rainha da Etiópia. Ele tinha ido a Jerusalém para adorar a Deus. Na volta, sentado na sua carruagem, ele estava lendo o livro do profeta Isaías. <u>29</u>Então o Espírito Santo disse a Filipe:</p><h1>Deus</h1><h2>Chegue perto dessa carruagem e acompanhe-a.</h2><p><u>30</u>Filipe correu para perto da carruagem e ouviu o funcionário lendo o livro do profeta Isaías. Aí perguntou:</p><h1>Filipe, o Evangelista</h1><h4>O senhor entende o que está lendo?</h4><h1>O Eunuco Etíope</h1><h4><u>31</u>Como posso entender se ninguém me explica?</h4><p>respondeu o funcionário.<br />   Então convidou Filipe para subir e sentar-se com ele na carruagem. <u>32</u>A parte das Escrituras Sagradas que o funcionário estava lendo era esta:<br />   \"Ele era como um cordeiro que é levado para ser morto;<br />   era como uma ovelha que fica muda quando cortam a sua lã.<br />   Ele não disse nada.<br />   <u>33</u>Foi humilhado, e foram injustos com ele.<br />   Ninguém poderá falar a respeito de descendentes dele,<br />   já que a sua vida na terra chegou ao fim.\"<br />   <u>34</u>O funcionário perguntou a Filipe:</p><h1>O Eunuco Etíope</h1><h4>Por favor, me explique uma coisa! De quem é que o profeta está falando isso? É dele mesmo ou de outro?</h4><p><u>35</u>Então, começando com aquela parte das Escrituras, Filipe anunciou ao funcionário a boa notícia a respeito de Jesus.<br />   <u>36</u>Enquanto estavam viajando, chegaram a um lugar onde havia água. Então o funcionário disse:</p><h1>O Eunuco Etíope</h1><h4>Veja! Aqui tem água. Será que eu não posso ser batizado?</h4><p><u>37</u>[Filipe respondeu:</p><h1>Filipe, o Evangelista</h1><h4>Se o senhor crê de todo o coração, é claro que pode.</h4><p>E o funcionário disse:</p><h1>O Eunuco Etíope</h1><h4>Sim, eu creio que Jesus Cristo é o Filho de Deus.]</h4><p><u>38</u>Ele mandou parar a carruagem, os dois entraram na água, e Filipe o batizou ali.<br />   <u>39</u>Quando eles estavam saindo da água, o Espírito do Senhor levou Filipe embora. O funcionário não viu mais Filipe, porém continuou a sua viagem, cheio de alegria. <u>40</u>De repente, Filipe se encontrou na cidade de Azoto e seguiu viagem, anunciando o evangelho por todas as cidades até chegar a Cesaréia.<br />   <strong>9</strong><u>1</u>Enquanto isso, Saulo não parava de ameaçar de morte os seguidores do Senhor Jesus. Ele foi falar com o Grande Sacerdote <u>2</u>e pediu cartas de apresentação para as sinagogas da cidade de Damasco. Com esses documentos Saulo poderia prender e levar para Jerusalém os seguidores do Caminho do Senhor que moravam ali, tanto os homens como as mulheres.<br />   <u>3</u>Mas na estrada de Damasco, quando Saulo já estava perto daquela cidade, de repente, uma luz que vinha do céu brilhou em volta dele. <u>4</u>Ele caiu no chão e ouviu uma voz que dizia:</p><h1>Jesus</h1><h2>Saulo, Saulo, por que você me persegue?</h2><h1>Paulo</h1><h3><u>5</u>Quem é o senhor?</h3><p>perguntou ele.<br />   A voz respondeu:</p><h1>Jesus</h1><h2>Eu sou Jesus, aquele que você persegue. <u>6</u>Mas levante-se, entre na cidade, e ali dirão a você o que deve fazer.</h2><p><u>7</u>Os homens que estavam viajando com Saulo ficaram parados sem poder dizer nada. Eles ouviram a voz, mas não viram ninguém. <u>8</u>Saulo se levantou do chão e abriu os olhos, mas não podia ver nada. Então eles o pegaram pela mão e o levaram para Damasco. <u>9</u>Ele ficou três dias sem poder ver e durante esses dias não comeu nem bebeu nada.<br />   <u>10</u>Em Damasco morava um seguidor de Jesus chamado Ananias. Ele teve uma visão, e nela apareceu o Senhor, chamando:</p><h1>Deus</h1><h2>Ananias!</h2><p>Ele respondeu:</p><h1>Ananias de Damasco</h1><h4>Aqui estou, Senhor!</h4><p><u>11</u>E o Senhor lhe disse:</p><h1>Deus</h1><h2>Apronte-se, e vá até a casa de Judas, na rua Direita, e procure um homem chamado Saulo, da cidade de Tarso. Ele está orando <u>12</u>e teve uma visão. Nela apareceu um homem chamado Ananias, que entrou e pôs as mãos sobre ele a fim de que ele pudesse ver de novo.</h2><p><u>13</u>Ananias respondeu:</p><h1>Ananias de Damasco</h1><h4>Senhor, muita gente tem me falado a respeito desse homem e de todas as maldades que ele fez em Jerusalém com os que crêem no Senhor. <u>14</u>E agora ele veio aqui a Damasco com autorização dos chefes dos sacerdotes para prender todos os que te adoram.</h4><p><u>15</u>Mas o Senhor disse a Ananias:</p><h1>Deus</h1><h2>Vá, pois eu escolhi esse homem para trabalhar para mim, a fim de que ele anuncie o meu nome aos não-judeus, aos reis e ao povo de Israel. <u>16</u>Eu mesmo vou mostrar a Saulo tudo o que ele terá de sofrer por minha causa.</h2><p><u>17</u>Então Ananias foi, entrou na casa de Judas, pôs as mãos sobre Saulo e disse:</p><h1>Ananias de Damasco</h1><h4>Saulo, meu irmão, o Senhor que me mandou aqui é o mesmo Jesus que você viu na estrada de Damasco. Ele me mandou para que você veja de novo e fique cheio do Espírito Santo.</h4><p><u>18</u>No mesmo instante umas coisas parecidas com escamas caíram dos olhos de Saulo, e ele pôde ver de novo. Ele se levantou e foi batizado; <u>19</u>depois ele comeu alguma coisa e ficou forte como antes.<br />   Saulo ficou alguns dias com os seguidores de Jesus em Damasco. <u>20</u>E começou imediatamente a anunciar Jesus nas sinagogas, dizendo:</p><h1>Paulo</h1><h3>Jesus é o Filho de Deus.</h3><p><u>21</u>Todos os que ouviam Saulo ficavam admirados e perguntavam:</p><h1>Judeus em Damasco</h1><h4>Não é este o homem que em Jerusalém estava matando todos os seguidores de Jesus? Não foi ele que veio até aqui para prender e levar essa gente aos chefes dos sacerdotes?</h4><p><u>22</u>Mas as mensagens de Saulo se tornavam cada vez mais poderosas. E as provas que ele apresentava de que Jesus era o Messias eram tão fortes, que os judeus que moravam em Damasco não sabiam o que dizer.<br />   <u>23</u>Muitos dias depois, os judeus de Damasco se reuniram e resolveram matá-lo, <u>24</u>mas Saulo ficou sabendo do plano deles. Eles vigiavam os portões da cidade dia e noite para o matar. <u>25</u>Mas certa noite os seguidores de Saulo o puseram dentro de um cesto e o desceram por uma abertura que havia na muralha da cidade.<br />   <u>26</u>Saulo foi para Jerusalém e tentou juntar-se aos seguidores de Jesus. Porém todos tinham medo dele porque não acreditavam que ele também era seguidor de Jesus. <u>27</u>Então Barnabé veio ajudá-lo e o apresentou aos apóstolos. E lhes contou como Saulo tinha visto o Senhor no caminho e como o Senhor havia falado com ele. Barnabé também contou como, em Damasco, Saulo, pelo poder do nome de Jesus, havia anunciado corajosamente o evangelho. <u>28</u>Depois disso Saulo ficou com eles, andando por toda parte em Jerusalém; e, pelo poder do nome do Senhor, ele anunciava corajosamente o evangelho. <u>29</u>Ele também conversava e discutia com os judeus que tinham sido criados fora da terra de Israel, mas eles procuravam um jeito de matá-lo. <u>30</u>Quando os irmãos souberam disso, levaram Saulo até a cidade de Cesaréia e depois o mandaram para a cidade de Tarso.<br />   <u>31</u>Em toda a região da Judéia, Galiléia e Samaria, a Igreja estava em paz. Ela ficava cada vez mais forte, crescia em número de pessoas com a ajuda do Espírito Santo e mostrava grande respeito pelo Senhor Jesus.</p>","path":"/355","Question1":"List all the questions raised in the text. Who makes them? To whom are they made?","Question2":"Is there a question that stands out to you? Why?","Question3":"What questions would you like to be able to ask the author of this text?","Question4":"What implications do these questions from the text have for your life?","prevPath":"/354","nextPath":"/356"}},"pathContext":{}}

